<template>
  <div>
    <div
      class="d-flex justify-content-start align-items-center mb-5"
      style="margin-left: 5%"
    >
      <!-- Ï¥ù Í±¥Ïàò -->
      <div style="display: flex; align-items: center">
        <span class="count"> Ï¥ù {{ totalTargetsCount }}Í±¥ </span>
      </div>

      <div class="radio-container p-3 lg2">
        <input
          class="radio-input"
          type="radio"
          name="targetList"
          id="finished"
          value="finished"
          v-model="sortBy"
        />
        <label class="radio-label" for="finished">
          <span class="custom-radio"></span>
          Ï¢ÖÎ£å
        </label>
      </div>
      <div class="radio-container p-3 lg2">
        <input
          class="radio-input"
          type="radio"
          name="targetList"
          id="notFinished"
          value="not-finished"
          v-model="sortBy"
        />
        <label class="radio-label" for="notFinished">
          <span class="custom-radio"></span>
          ÏßÑÌñâÏ§ë
        </label>
      </div>
    </div>
    <!-- Î™©ÌëúÍ∞Ä ÏóÜÎäî Í≤ΩÏö∞ Î©îÏãúÏßÄ ÌëúÏãú -->
    <div
      v-if="filteredMileages.length === 0"
      class="text-center mb-4"
      style="color: gray"
    >
      ÏßÑÌñâ Ï§ëÏù∏ Î™©ÌëúÍ∞Ä ÏóÜÏäµÎãàÎã§.
    </div>

    <div class="row">
      <div
        v-for="(targets, index) in filteredMileages"
        :key="targets.target_no"
        class="col-md-4 mb-3"
      >
        <div class="p-3">
          <div
            :style="{
              backgroundColor:
                backgroundColors[index % backgroundColors.length],
            }"
            class="mx-auto rounded-4 target-box"
          >
            <div
              class="py-1"
              style="
                display: flex;
                align-items: center;
                margin-bottom: -10px;
                position: relative;
              "
            >
              <span
                :class="getStatusClass(targets)"
                class="status-label"
                style="margin-left: 20px"
                >{{ getStatusText(targets) }}</span
              >
              <div
                style="
                  text-align: left;
                  margin-left: 10px;
                  margin-top: 10px;
                  font-weight: bold;
                  font-family: KB_C2;
                  font-size: 13pt;
                "
                class="mb-2"
              >
                {{ targets.mile_name }} ÎßàÏùºÎ¶¨ÏßÄ
              </div>
              <div class="button-container">
                <button
                  class="delete-button"
                  @click="deleteTarget(targets.target_no)"
                >
                  ÏÇ≠Ï†ú
                </button>
              </div>
            </div>
            <div
              class="py-3"
              style="
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                margin-left: 20px;
              "
            >
              <span
                class="lg2"
                style="
                  font-family: 'KB_C1';
                  font-size: 20px;
                  margin-bottom: -25px;
                "
                >Í∞úÏù∏ ÏßÑÌñâÎ•†</span
              ><br />
              <div
                style="
                  display: flex;
                  justify-content: space-between;
                  align-items: center;
                  width: 100%;
                "
              >
                <div
                  class="progress"
                  role="progressbar"
                  aria-label="Animated striped example"
                  aria-valuenow="60"
                  aria-valuemin="0"
                  aria-valuemax="100"
                  style="width: 80%; margin-right: 10px"
                >
                  <div
                    class="progress-bar progress-bar-striped progress-bar-animated"
                    :style="{
                      width: calculateProgress(targets),
                      backgroundColor: '#FB773C',
                    }"
                  ></div>
                </div>
                <span
                  style="
                    font-weight: bold;
                    white-space: nowrap;
                    font-family: 'KB_C2';
                    font-size: 25px;
                    margin-right: 10px;
                  "
                >
                  {{ calculateProgress(targets) }}</span
                >
              </div>
              <span
                class="md"
                style="margin-top: 5px; font-size: 13px; font-family: 'KB_C2'"
                >{{ targets.start_date }} - {{ targets.end_date }}</span
              >
            </div>

            <span class="lg2" style="font-family: 'KB_C1'; font-size: 17px">
              üéØ ÎÇòÏùò ÎßàÏùºÎ¶¨ÏßÄ / Î™©Ìëú ÎßàÏùºÎ¶¨ÏßÄ
            </span>
            <div class="py-3">
              <span class="bold-x-lg" style="font-family: 'KB_C1'">
                <span class="highlight-score">{{
                  targets.achievementRate
                }}</span>
                / {{ targets.target_mileage }}</span
              >
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="d-flex justify-content-center" style="margin-top: 12vh">
      <button class="addbtn" @click="openModal">Î™©Ìëú Ï∂îÍ∞Ä</button>
    </div>

    <!-- Î™®Îã¨ -->
    <div v-if="isModalOpen" class="modal-overlay">
      <div class="modal-content">
        <div class="modal-header">
          <h2 class="modal-title">Í∞úÏù∏Î≥Ñ Î™©Ìëú ÏÑ§Ï†ïÌïòÍ∏∞</h2>
          <button class="close-button" @click="closeModal">&times;</button>
        </div>
        <div class="modal-body">
          <div class="form-group">
            <label for="mileage-select">Î™©Ìëú ÎßàÏùºÎ¶¨ÏßÄ</label>
            <div class="select-wrapper">
              <select
                v-model="selectedLabel"
                id="mileage-select"
                class="form-select"
              >
                <option v-for="label in labels" :key="label" :value="label">
                  {{ label.mile_name }}
                </option>
              </select>
              <i class="bi bi-caret-down-fill select-icon"></i>
            </div>
          </div>
          <div class="form-group">
            <label>Î™©Ìëú Í∏∞Í∞Ñ</label>
            <div class="date-range">
              <Datepicker
                v-model="startDate"
                :format="formatDate"
                placeholder="ÏãúÏûëÏùº"
              />
              <span class="date-separator">~</span>
              <Datepicker
                v-model="endDate"
                :format="formatDate"
                placeholder="Ï¢ÖÎ£åÏùº"
              />
            </div>
          </div>
          <div class="form-group">
            <label for="mileage-score">Î™©Ìëú ÎßàÏùºÎ¶¨ÏßÄ Ï†êÏàò</label>
            <input type="number" id="mileage-score" v-model="targetScore" />
          </div>
        </div>
        <div class="modal-footer">
          <button class="submit-button" @click="addAction">Îì±Î°ù</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters, mapState } from 'vuex';
import api from '@/api/axios';
import Datepicker from '@vuepic/vue-datepicker';
import '@vuepic/vue-datepicker/dist/main.css';
import Swal from 'sweetalert2';

export default {
  name: 'PersonalTarget',
  emits: ['dataLoaded'], // Ïù¥ Ïù¥Î≤§Ìä∏Î•º Ïª¥Ìè¨ÎÑåÌä∏ÏóêÏÑú Ï≤òÎ¶¨Ìï®ÏùÑ ÏÑ†Ïñ∏
  components: {
    Datepicker, //Ïù¥ Ïª¥Ìè¨ÎÑåÌä∏Í∞Ä ÏÇ¨Ïö©Ìï† ÌïòÏúÑ Ïª¥Ìè¨ÎÑåÌä∏
  },
  data() {
    return {
      mileages: [],
      isModalOpen: false,
      labels: [],
      selectedLabel: null,
      startDate: null,
      endDate: null,
      targetScore: 0,
      backgroundColors: ['#F5F5F5'],
      sortBy: 'not-finished',
      //  dropDownVisible: {},
    };
  },
  methods: {
    ...mapActions('target', [
      'addPersonalTarget',
      'fetchPersonalTargets',
      'deleteTarget',
    ]),

    loginInfo() {
      return this.getLoginInfo;
    },
    isChecked() {
      return this.getIsChecked;
    },
    isLoggedIn() {
      return !!this.loginInfo;
    },

    async fetchMileages() {
      // ÎßàÏùºÎ¶¨ÏßÄ Ïπ¥ÌÖåÍ≥†Î¶¨ Í∞ÄÏ†∏Ïò§Í∏∞
      try {
        const response = await api.get('/notice/mileage');
        console.log('fetchMileages Î©îÏÜåÎìú :', response.data); // ÏùëÎãµ Îç∞Ïù¥ÌÑ∞Î•º ÏΩòÏÜîÏóê Ï∂úÎ†•
        this.labels = response.data;
      } catch (error) {
        console.error(
          'Error fetching mileages:',
          error.response ? error.response.data : error.message
        );
      }
    },

    handleCheckboxChange(sortType) {
      if (sortType === 'finished' && this.sortByFin) {
        this.sortByNotFin = false;
      } else if (sortType === 'not-finished' && this.sortByNotFin) {
        this.sortByFin = false;
      }
    },
    formatDate(date) {
      const year = date.getFullYear();
      const month = String(date.getMonth() + 1).padStart(2, '0');
      const day = String(date.getDate()).padStart(2, '0');
      return `${year}-${month}-${day}`;
    },
    openModal() {
      this.isModalOpen = true;
    },
    closeModal() {
      this.isModalOpen = false;
    },
    async addAction() {
      const target = {
        mile_no: this.selectedLabel.mile_no,
        user_no: this.loginInfo.user_no, // Ïú†Ï†Ä ÏïÑÏù¥ÎîîÎ•º Ï†ÅÏ†àÌûà Î≥ÄÍ≤Ω
        start_date: this.startDate,
        end_date: this.endDate,
        target_mileage: this.targetScore,
        is_together: false,
        is_manager_plan: false,
        month: new Date().getMonth() + 1, //1~12Ïõî Î≤îÏúÑÎ•º ÎßûÏ∂îÍ∏∞ ÏúÑÌï¥ÏÑú
        achievementRate: 0, //Î™©Ìëú Îã¨ÏÑ±Î•†
      };
      console.log('targetÏù¥ ÏÑúÎ≤ÑÎ°ú Îç∞Ïù¥ÌÑ∞ ÎÑòÍ∏∞Îäî Í≤É :', target);
      try {
        await this.addPersonalTarget(target);
        this.closeModal();
        window.location.reload(); // ÌéòÏù¥ÏßÄ ÏÉàÎ°úÍ≥†Ïπ® Ï∂îÍ∞Ä
      } catch (error) {
        console.error(
          'Error adding target:',
          error.response ? error.response.data : error.message
        );
      }
    },
    calculateProgress(target) {
      if (!target.target_mileage || target.target_mileage === 0) return '0%'; // Î™©Ìëú ÎßàÏùºÎ¶¨ÏßÄÍ∞Ä 0Ïù¥Î©¥ ÏßÑÌñâÎ•†ÏùÄ 0
      if (this.getStatusText(target) === 'ÏòàÏ†ï') return '0%'; // ÏòàÏ†ï ÏÉÅÌÉúÏù¥Î©¥ ÏßÑÌñâÎ•†ÏùÄ 0%
      const progress = (target.achievementRate / target.target_mileage) * 100;
      return progress > 100 ? '100%' : Math.round(progress) + '%'; // ÏµúÎåÄ ÏßÑÌñâÎ•†ÏùÄ 100%
    },

    getStatusClass(target) {
      const currentDate = new Date();
      const startDate = new Date(target.start_date);
      const endDate = new Date(target.end_date);

      if (currentDate < startDate) {
        return 'upcoming';
      } else if (currentDate > endDate) {
        return 'completed';
      } else {
        return 'ongoing';
      }
    },
    getStatusText(target) {
      const currentDate = new Date();
      const startDate = new Date(target.start_date);
      const endDate = new Date(target.end_date);

      if (currentDate < startDate) {
        return 'ÏòàÏ†ï';
      } else if (currentDate > endDate) {
        return 'Ï¢ÖÎ£å';
      } else {
        return 'ÏßÑÌñâÏ§ë';
      }
    },
    sortTargets(targets) {
      return targets.sort((a, b) => {
        const statusA = this.getStatusText(a);
        const statusB = this.getStatusText(b);

        if (statusA === 'ÏßÑÌñâÏ§ë' && statusB !== 'ÏßÑÌñâÏ§ë') {
          return -1;
        }
        if (statusA !== 'ÏßÑÌñâÏ§ë' && statusB === 'ÏßÑÌñâÏ§ë') {
          return 1;
        }
        if (statusA === 'ÏòàÏ†ï' && statusB !== 'ÏòàÏ†ï') {
          return -1; // 'ÏòàÏ†ï'Ïù∏ Í≤ΩÏö∞ 'Ï¢ÖÎ£å' ÏïûÏúºÎ°ú Ï†ïÎ†¨
        }
        if (statusA !== 'ÏòàÏ†ï' && statusB === 'ÏòàÏ†ï') {
          return 1;
        }
        // 'Ï¢ÖÎ£å' ÏÉÅÌÉúÎäî ÏûêÎèôÏúºÎ°ú Îß® Îí§Î°ú Ï†ïÎ†¨Îê®
        return 0;
      });
    },

    filteredTargets(filter) {
      const currentDate = new Date();
      return this.adminTargets.filter((target) => {
        const endDate = new Date(target.end_date);
        if (filter === 'finished') {
          return currentDate > endDate;
        } else {
          return currentDate <= endDate;
        }
      });
    },
    sortedAdminTargets(targets) {
      return targets.sort((a, b) => a.mile_no - b.mile_no);
    },

    async loadUserParticipatedTargets() {
      try {
        const targetNos = this.adminTargets.map((target) => target.target_no);
        console.log('5. Target numbers:', targetNos);

        for (const targetNo of targetNos) {
          const isParticipating = await this.checkParticipation(targetNo);
          if (isParticipating) {
            this.userParticipatedTargets.push(targetNo);
          }
        }

        console.log(
          '7. Updated userParticipatedTargets:',
          this.userParticipatedTargets
        );
      } catch (error) {
        console.error('Failed to load user participated targets:', error);
      } finally {
        this.isLoading = false; // Î°úÎî© ÏÉÅÌÉú Ìï¥Ï†ú
      }
    },
    async deleteTarget(targetNo) {
      Swal.fire({
        title: 'Í≤ΩÍ≥†',
        text: 'Ï†ïÎßêÎ°ú ÏÇ≠Ï†úÌïòÏãúÍ≤†ÏäµÎãàÍπå?',
        icon: 'warning',
        showCancelButton: true,
        scrollbarPadding: false,
        confirmButtonText: 'ÌôïÏù∏',
        cancelButtonText: 'Ï∑®ÏÜå',
        reverseButtons: false,
        willOpen: () => {
          // Ïä§ÌÅ¨Î°§Î∞î Ìå®Îî© Ï†úÍ±∞
          document.body.style.paddingRight = '0px';
        },
        willClose: () => {
          // Ïä§ÌÅ¨Î°§Î∞î Ìå®Îî© Ï†úÍ±∞
          document.body.style.paddingRight = '0px';
        },
      }).then(async (result) => {
        if (result.isConfirmed) {
          try {
            const userNo = this.$store.getters['login/getLoginInfo'].user_no;
            await this.$store.dispatch('target/deleteTarget', {
              userNo,
              targetNo,
            });
            Swal.fire({
              title: 'ÏÇ≠Ï†ú ÏôÑÎ£å',
              text: 'ÏÇ≠Ï†úÍ∞Ä ÏôÑÎ£åÎêòÏóàÏäµÎãàÎã§.',
              icon: 'success',
              scrollbarPadding: false,
              willOpen: () => {
                document.body.style.paddingRight = '0px';
              },
              willClose: () => {
                document.body.style.paddingRight = '0px';
              },
            });
          } catch (error) {
            console.error('Error deleting target:', error);
            Swal.fire({
              title: 'Ïò§Î•ò Î∞úÏÉù',
              text: 'ÏÇ≠Ï†ú Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§. Îã§Ïãú ÏãúÎèÑÌï¥ Ï£ºÏÑ∏Ïöî.',
              icon: 'error',
              scrollbarPadding: false,
              willOpen: () => {
                document.body.style.paddingRight = '0px';
              },
              willClose: () => {
                document.body.style.paddingRight = '0px';
              },
            });
          }
        } else {
          Swal.fire({
            title: 'Ï∑®ÏÜåÎê®',
            text: 'ÏÇ≠Ï†úÍ∞Ä Ï∑®ÏÜåÎêòÏóàÏäµÎãàÎã§.',
            icon: 'info',
            scrollbarPadding: false,
            willOpen: () => {
              document.body.style.paddingRight = '0px';
            },
            willClose: () => {
              document.body.style.paddingRight = '0px';
            },
          });
        }
      });
    },
    showAlert(message, icon) {
      this.$swal({
        title: message,
        icon: icon,
        confirmButtonText: 'ÌôïÏù∏',
        confirmButtonColor: '#4b4a4a',
        allowOutsideClick: false,
        allowEscapeKey: false,
        allowEnterKey: false,
        stopKeydownPropagation: false,
        scrollbarPadding: false,
        backdrop: true,
        didOpen: () => {
          document.body.classList.add('no-scroll');
          document.documentElement.style.overflow = 'hidden';
        },
        willClose: () => {
          document.body.classList.remove('no-scroll');
          document.documentElement.style.overflow = '';
        },
      });
    },
  },

  async created() {
    console.log('targer loginInfo Ïù¥ Ï∞çÌûàÎÇòÏöî ?:', this.loginInfo);
    console.log('isLoggedIn:', this.isLoggedIn);
    console.log('isChecked:', this.isChecked);
    this.isLoading = true;
    try {
      await this.fetchMileages();
      await this.fetchPersonalTargets(this.loginInfo.user_no).then(() => {
        console.log('Targets after fetch:', this.getPersonalTargets);
      });
    } catch (error) {
      console.error('Error initializing component:', error);
    } finally {
      this.isLoading = false;
    }
  },

  computed: {
    ...mapGetters('login', ['getLoginInfo', 'getIsChecked']),
    ...mapState('login', ['loginInfo']),
    ...mapGetters('target', ['getPersonalTargets']),

    adminTargets() {
      return this.getPersonalTargets || []; // getTargetsÍ∞Ä undefinedÏùº Í≤ΩÏö∞ Îπà Î∞∞Ïó¥ Î∞òÌôò
    },
    displayedTargets() {
      const filtered = this.filteredTargets(this.sortBy);
      const sorted = this.sortedAdminTargets(filtered);
      return this.sortBy === 'not-finished' ? this.sortTargets(sorted) : sorted;
    },
    totalTargetsCount() {
      return this.displayedTargets.length;
    },
    targets() {
      const targets = this.getPersonalTargets;
      console.log('Targets:', targets);
      return Array.isArray(targets) ? targets : []; // Î∞∞Ïó¥Ïù¥ ÏïÑÎãàÎ©¥ Îπà Î∞∞Ïó¥ÏùÑ Î∞òÌôò
    },

    filteredMileages() {
      const currentDate = new Date();
      // mile_no Í∏∞Ï§ÄÏúºÎ°ú Ïò§Î¶ÑÏ∞®Ïàú Ï†ïÎ†¨

      const sortedTargets = [...this.targets]
        .sort((a, b) => a.mile_no - b.mile_no)
        .sort((a, b) => {
          const statusA = this.getStatusText(a);
          const statusB = this.getStatusText(b);
          if (statusA === 'ÏßÑÌñâÏ§ë' && statusB !== 'ÏßÑÌñâÏ§ë') return -1;
          if (statusA !== 'ÏßÑÌñâÏ§ë' && statusB === 'ÏßÑÌñâÏ§ë') return 1;
          if (statusA === 'ÏòàÏ†ï' && statusB !== 'ÏòàÏ†ï') return -1;
          if (statusA !== 'ÏòàÏ†ï' && statusB === 'ÏòàÏ†ï') return 1;
          return 0;
        });

      return sortedTargets.filter((mileage) => {
        if (!mileage || !mileage.end_date) {
          console.warn('Skipping mileage due to missing endDate:', mileage);
          return false;
        }

        const endDate = new Date(mileage.end_date);
        if (isNaN(endDate.getTime())) {
          console.warn('Invalid date format in endDate:', mileage.end_date);
          return false;
        }

        if (this.sortBy === 'finished') {
          return endDate < currentDate;
        } else if (this.sortBy === 'not-finished') {
          return endDate >= currentDate;
        } else {
          return true;
        }
      });
    },
  },
};
</script>

<style scoped>
.mb-3 {
  margin-bottom: 1rem; /* ÌïòÎã® Ïó¨Î∞± Ï∂îÍ∞Ä */
}
.target-box:hover {
  transform: scale(1.05); /* Ìò∏Î≤Ñ Ïãú ÌÅ¨Í∏∞Î•º 1.05Î∞∞Î°ú ÌôïÎåÄ */
}

.target-box:hover {
  transform: scale(1.05); /* ÎßàÏö∞Ïä§Î•º Ïò¨Î†∏ÏùÑ Îïå 5% ÌôïÎåÄ */
}
.status-label {
  display: inline-block;
  padding: 0.2em 0.6em;
  font-size: 12pt;
  font-family: KB_C2;
  /* font-weight: bold; */
  border-radius: 12px;
  margin-left: 10px; /* ÎßàÏùºÎ¶¨ÏßÄ Ïù¥Î¶ÑÍ≥º ÏÉÅÌÉú ÎùºÎ≤® ÏÇ¨Ïù¥Ïùò Í∞ÑÍ≤© Ï°∞Ï†ï */
}

.status-label.ongoing {
  background-color: #5cb85c; /* Ï¥àÎ°ùÏÉâ - ÏßÑÌñâÏ§ë */
  color: white;
}

.status-label.completed {
  background-color: #d9534f; /* Îπ®Í∞ÑÏÉâ - Ï¢ÖÎ£å */
  color: white;
}

.status-label.upcoming {
  background-color: #f0ad4e; /* Ï£ºÌô©ÏÉâ - ÏòàÏ†ï */
  color: white;
}

.radio-container {
  display: flex;
  align-items: center;
  cursor: pointer;
  position: relative;
}

.radio-input {
  display: none; /* Í∏∞Î≥∏ ÎùºÎîîÏò§ Î≤ÑÌäºÏùÑ Ïà®ÍπÅÎãàÎã§ */
}

.custom-radio {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 2px solid #ccc;
  margin-right: 10px;
  display: inline-block;
  position: relative;
}

.radio-input:checked + .radio-label .custom-radio {
  background-color: #fc8a58; /* Ï≤¥ÌÅ¨Îêú ÏÉÅÌÉúÏùº Îïå Î∞∞Í≤ΩÏÉâ */
  border: none; /* Ï≤¥ÌÅ¨Îêú ÏÉÅÌÉúÏùº Îïå ÌÖåÎëêÎ¶¨ Ï†úÍ±∞ */
}

.radio-input:checked + .radio-label .custom-radio::after {
  content: '‚úì';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  font-size: 14px; /* Ï≤¥ÌÅ¨Îêú ÏÉÅÌÉúÏùº Îïå Ï≤¥ÌÅ¨ ÌëúÏãú Ïä§ÌÉÄÏùº */
}

.radio-label {
  display: flex; /* labelÏùÑ flexÎ°ú ÏÑ§Ï†ïÌïòÏó¨ Ï†ÑÏ≤¥Í∞Ä ÌÅ¥Î¶≠ Í∞ÄÎä•ÌïòÎèÑÎ°ù */
  align-items: center;
  font-family: 'KB_C2';
  font-size: 20px;
  color: #333; /* ÎùºÎ≤® ÌÖçÏä§Ìä∏ ÏÉâÏÉÅ */
}

.highlight-score {
  background-color: #ffe2b5;
  padding: 2px 4px;
  border-radius: 20px;
}

.participation-info {
  display: flex;
  align-items: center;
  opacity: 1;
